.analytics {
  position: relative;
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.analytics .analytics-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
  gap: 2rem;
}
.analytics .analytics-header .header-left .main-title {
  font-size: 2rem;
  font-weight: 700;
  color: #111827;
  margin: 0 0 0.5rem 0;
}
.analytics .analytics-header .header-left .subtitle {
  font-size: 1.125rem;
  color: #6b7280;
  margin: 0;
}
.analytics .analytics-header .header-right {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.analytics .analytics-header .header-right .period-dropdown {
  position: relative;
  min-width: 150px;
}
.analytics .analytics-header .header-right .period-dropdown .period-select {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  background-color: white;
  appearance: none;
  cursor: pointer;
}
.analytics .analytics-header .header-right .period-dropdown .period-select:focus {
  outline: none;
  border-color: #3b82f6;
}
.analytics .analytics-header .header-right .period-dropdown .dropdown-arrow {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1rem;
  height: 1rem;
  color: #6b7280;
  pointer-events: none;
}
.analytics .analytics-header .header-right .export-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: white;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  cursor: pointer;
  transition: all 0.2s;
}
.analytics .analytics-header .header-right .export-btn:hover {
  background-color: #f9fafb;
  border-color: #9ca3af;
}
.analytics .analytics-header .header-right .export-btn svg {
  color: #6b7280;
}
.analytics .metric-cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.analytics .metric-cards-row .metric-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  position: relative;
}
.analytics .metric-cards-row .metric-card .card-content .metric-value {
  font-size: 2rem;
  font-weight: 700;
  color: #111827;
  line-height: 1;
  margin-bottom: 0.5rem;
}
.analytics .metric-cards-row .metric-card .card-content .metric-value.warning {
  color: #f59e0b;
}
.analytics .metric-cards-row .metric-card .card-content .metric-label {
  font-size: 1rem;
  color: #374151;
  font-weight: 600;
  margin-bottom: 0.5rem;
}
.analytics .metric-cards-row .metric-card .card-content .metric-change {
  font-size: 0.875rem;
  color: #6b7280;
}
.analytics .metric-cards-row .metric-card .card-content .metric-change.positive {
  color: #10b981;
}
.analytics .metric-cards-row .metric-card .metric-icon {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  width: 2rem;
  height: 2rem;
  color: #9ca3af;
}
.analytics .metric-cards-row .metric-card.turnover .metric-icon {
  color: #10b981;
}
.analytics .metric-cards-row .metric-card.carrying-cost .metric-icon {
  color: #3b82f6;
}
.analytics .metric-cards-row .metric-card.dead-stock .metric-icon {
  color: #f59e0b;
}
.analytics .metric-cards-row .metric-card.avg-days .metric-icon {
  color: #8b5cf6;
}
.analytics .analytics-tabs {
  display: flex;
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 0.25rem;
  margin-bottom: 2rem;
  overflow-x: auto;
}
.analytics .analytics-tabs .tab-button {
  display: flex;
  align-items: center;
  padding: 0.75rem 1.5rem;
  background: none;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}
.analytics .analytics-tabs .tab-button:hover {
  color: #374151;
  background-color: #f9fafb;
}
.analytics .analytics-tabs .tab-button.active {
  background-color: #3b82f6;
  color: white;
}
.analytics .analytics-tabs .tab-button .tab-text {
  margin: 0;
}
.analytics .charts-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}
.analytics .charts-section .chart-panel {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  overflow: hidden;
}
.analytics .charts-section .chart-panel .panel-header {
  padding: 1.5rem 1.5rem 1rem 1.5rem;
  border-bottom: 1px solid #f3f4f6;
}
.analytics .charts-section .chart-panel .panel-header .panel-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 0.25rem 0;
}
.analytics .charts-section .chart-panel .panel-header .panel-subtitle {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0;
}
.analytics .charts-section .chart-panel .chart-container {
  padding: 1.5rem;
  min-height: 300px;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid {
  display: flex;
  width: 100%;
  height: 200px;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .y-axis {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: 1rem;
  font-size: 0.75rem;
  color: #9ca3af;
  font-weight: 500;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area {
  flex: 1;
  position: relative;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area .grid-lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area .grid-lines .grid-line {
  position: absolute;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #f3f4f6;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area .grid-lines .grid-line:nth-child(1) {
  top: 0;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area .grid-lines .grid-line:nth-child(2) {
  top: 25%;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area .grid-lines .grid-line:nth-child(3) {
  top: 50%;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area .grid-lines .grid-line:nth-child(4) {
  top: 75%;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area .grid-lines .grid-line:nth-child(5) {
  top: 100%;
}
.analytics .charts-section .chart-panel .chart-container .chart-placeholder .chart-grid .chart-area .x-axis {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 0.75rem;
  color: #9ca3af;
  font-weight: 500;
}
.analytics .charts-section .chart-panel .chart-container .line-chart {
  height: 100%;
  display: flex;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .y-axis {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-right: 1rem;
  font-size: 0.75rem;
  color: #9ca3af;
  font-weight: 500;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area {
  flex: 1;
  position: relative;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .grid-lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .grid-lines .grid-line {
  position: absolute;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #f3f4f6;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .grid-lines .grid-line:nth-child(1) {
  top: 0;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .grid-lines .grid-line:nth-child(2) {
  top: 25%;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .grid-lines .grid-line:nth-child(3) {
  top: 50%;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .grid-lines .grid-line:nth-child(4) {
  top: 75%;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .grid-lines .grid-line:nth-child(5) {
  top: 100%;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .trend-line {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
.analytics .charts-section .chart-panel .chart-container .line-chart .chart-area .x-axis {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  padding: 0 1rem;
  font-size: 0.75rem;
  color: #9ca3af;
  font-weight: 500;
}
.analytics .voice-assistant {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
}
.analytics .voice-assistant:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.analytics .voice-assistant svg {
  color: white;
}

@media (max-width: 1024px) {
  .analytics .charts-section {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
}
@media (max-width: 768px) {
  .analytics {
    padding: 1rem;
  }
  .analytics .analytics-header {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
  }
  .analytics .analytics-header .header-right {
    justify-content: stretch;
  }
  .analytics .analytics-header .header-right .period-dropdown {
    min-width: auto;
  }
  .analytics .analytics-header .header-right .export-btn {
    flex: 1;
    justify-content: center;
  }
  .analytics .metric-cards-row {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 1rem;
  }
  .analytics .metric-cards-row .metric-card {
    padding: 1rem;
  }
  .analytics .metric-cards-row .metric-card .card-content .metric-value {
    font-size: 1.5rem;
  }
  .analytics .metric-cards-row .metric-card .card-content .metric-label {
    font-size: 0.875rem;
  }
  .analytics .metric-cards-row .metric-card .metric-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
  .analytics .analytics-tabs .tab-button {
    padding: 0.5rem 1rem;
    font-size: 0.75rem;
  }
  .analytics .charts-section .chart-panel .chart-container {
    padding: 1rem;
    min-height: 250px;
  }
}
@media (max-width: 480px) {
  .analytics .analytics-header .header-left .main-title {
    font-size: 1.5rem;
  }
  .analytics .analytics-header .header-left .subtitle {
    font-size: 1rem;
  }
  .analytics .metric-cards-row {
    grid-template-columns: 1fr;
  }
  .analytics .analytics-tabs .tab-button {
    padding: 0.5rem 0.75rem;
    font-size: 0.75rem;
  }
}
